<template>
  <div class="container-fluid about">
    <div class="row justify-content-center d-flex">
      <div class="col-12">
        <prismic-rich-text :field="biography" />
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
     biography: []
    }
  },
  methods: {
   async getData() {
 const data = await this.$prismic.client.getSingle('about')
 this.biography = data.data.biography
    }
  },
  mounted() {
    this.getData()
  }
}
</script>


